<template>
    <div>
        <div class="container howDraw underline">
            <el-button @click="btnDiamond1">钻石召唤单次召唤</el-button>
            <el-button @click="btnDiamond5">钻石召唤五连召唤</el-button>
            <el-button @click="btnCoin1">金币召唤单次召唤</el-button>
            <el-button @click="btnCoin5">金币召唤五次召唤</el-button><br>
<!--            <el-button @click="btnNew1">奇妙厨师卡池单次召唤</el-button>-->
<!--            <el-button @click="btnNew5">奇妙厨师卡池五次召唤</el-button>-->
            <el-button v-if="weekVisible" @click="btnCamp1">英灵殿卡池单次召唤</el-button>
            <el-button v-if="weekVisible" @click="btnCamp5">英灵殿卡池五次召唤</el-button><br v-if="weekVisible">
<!--            <el-button @click="btnTestNew1">新机制下奇妙厨师卡池单次召唤测试</el-button>-->
<!--            <el-button @click="btnTestNew5">新机制下奇妙厨师卡池五次召唤测试</el-button><br>-->
            <el-button @click="dialogVisible = true">{{ this.$t("african.accumulation") }}</el-button>
            <el-button @click="min = 100" style="opacity: 0">看毛看啊</el-button>
            <!--<el-button @click="toggleCards(96)">切换为测试服卡池（96张版本）</el-button>-->
            <!--<el-button @click="toggleCards(78)">切换为国际服卡池（90张版本）</el-button>-->
        </div>
        <div class="container cards" v-show="cards != []">
            <div v-for="(item,index) in cards"
                 :class="'card col-sm-2 col-xs-5 ' + item.class"
                 :key="index"
                 @click="open(index)"
            >
                <img src="../assets/image/cardback.jpg">
                <div class="cardfront" :style="'background-image: url('+item.pic+')'">
                    <div :class="'title ' + item.type">
                        <span>{{ item.cost }}</span>{{ item.name }}
                    </div>
                </div>
                <div class="txt">
                    <div :class="'rare ' + item.rare"></div>
                    {{ item.txt }}
                </div>
            </div>
        </div>
        <el-dialog
                :title="this.$t('african.accumulation')"
                :visible.sync="dialogVisible"
                width="90%">
            <div class="pay">
                {{ this.$t("african.hasCost") }}{{ coins }}{{ this.$t("african.gold") }}、{{ diamonds }}{{ this.$t("african.diamond") }}
            </div>
            <div class="cardAmounts">
                <div
                        v-show="item.amount"
                        v-for="(item,index) in european"
                        :key="index"
                        class="cardAmount col-sm-3 col-xs-6 "
                        :class="item.rare"
                >
                    <div>{{ item.name }}</div>
                    <div>{{ 'x' + item.amount }}</div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import moment from 'moment'
export default {
    name: "african",
    data() {
        return {
            equipmentList: [],
            herosList: [],
            cardPoolList: [],
            cardPool: {},
            african: [],
            european: [],
            version: 78,
            min: 1,
            cards: [],
            cardHeight: 0,
            times: 0,
            dialogVisible: false,
            weekVisible: false,
            coins: 0,
            diamonds: 0,

            gold: [],
            silver: [],
            copper: [],
        }
    },
    filter: {
        summonType(val) {
            switch (val) {
                case 1:
                    return 'l1'
                case 2:
                    return 'l2'
                case 3:
                    return 'l3'
                case 4:
                    return 'l4'
            }
        }
    },
    created() {
        this.init()
    },
    mounted() {
    },
    methods: {
        init() {
            this.european = []
            this.african = []
            this.gold = []
            this.silver = []
            this.copper = []
            this.$http.get('heros.json').then((res) => {
                this.herosList = res.data
                this.allocate(this.herosList, this.version)
            })
            this.$http.get('equipment.json').then((res) => {
                this.equipmentList = res.data
                this.allocate(this.equipmentList, this.version)
            })
            this.$http.get('fajiang.json').then((res) => {
                this.cardPoolList = res.data
            })
            if (moment().day() == 5 || moment().day() == 6 || moment().day() == 0){
                this.weekVisible = true
            }
        },
        allocate(obj, version) {
            switch (obj, version) {
                case 96:
                    obj.forEach((item) => {
                        if (item.rare == "gold") {
                            this.european.push(item);
                        } else {
                            this.european.push(item);
                            this.african.push(item);
                        }
                    })
                    break
                case 78:
                    obj.forEach((item) => {
                        if (item.if) {
                            if (item.rare == "gold") {
                                this.european.push(item);
                            } else {
                                this.european.push(item);
                                this.african.push(item);
                            }
                        }
                    })
                    break
            }
        },
        rareAllocate(obj) {
            obj.forEach((item) => {
                if (item.rare == "gold") {
                    this.gold.push(item);
                } else if (item.rare == 'silver') {
                    this.silver.push(item);
                } else {
                    this.copper.push(item)
                }
            })
        },
        roll100(min) {
            return Math.floor(Math.random() * (101 - min) + min)
        },
        rollN(n, min) {
            return Math.floor(Math.random() * (n + 1 - min) + min)
        },
        rollcard(cards) {
            return Math.floor(Math.random() * cards.length)
        },
        summon(card, type, gold, silver, copper) {
            if (card.rare == "copper") {
                console.log('l' + type + card.name + "*" + copper);
                this.european.forEach(item => {
                    if (item.name == card.name) {
                        item.amount = item.amount + copper
                    }
                })
                this.inin(card, type, card.name + "×" + copper);
            } else if (card.rare == "silver") {
                console.log('l' + type + card.name + "*" + silver);
                this.european.forEach(item => {
                    if (item.name == card.name) {
                        item.amount = item.amount + silver
                    }
                })
                this.inin(card, type, card.name + "×" + silver);
            } else {
                console.log('l' + type + card.name + "*" + gold);
                this.european.forEach(item => {
                    if (item.name == card.name) {
                        item.amount = item.amount + gold
                    }
                })
                this.inin(card, type, card.name + "×" + gold);
            }
        },
        // 权重决定卡
        probAllocate(rareArr) {
            let card = undefined
            let probTotal = 0 // eslint-disable-line no-unused-vars
            rareArr.forEach(item => {
                probTotal += item.prob
            })
            let roll_prob = Math.random() * probTotal
            for (let i = 0;i < rareArr.length;i ++){
                roll_prob -= rareArr[i].prob
                if (roll_prob < 0) {
                    // console.log('i---',i);
                    card = rareArr[i]
                    break
                }
            }
            return card
        },
        testUp1(id) {
            let card = undefined;
            let roll_no = this.roll100(this.min);
            let rare_no = this.roll100(this.min);
            console.log('rare_no---',rare_no);
            this.cardPoolList.forEach(item => {
                if (item.id == id){
                    this.cardPool = item
                }
            })
            let amount = this.cardPool.cards.length * (this.cardPool.prob - 1) + this.european.length
            this.cardPool.cards.forEach(cardP => {
                this.european.forEach(cardR => {
                    if (cardP == cardR.name){
                        cardR.prob = this.cardPool.prob / amount
                    }
                })
            })
            this.european.forEach(item => {
                if (!item.prob){
                    item.prob = 1 / amount
                }
            })

            // 决定稀有度
            if (rare_no <= 57) {
                this.rareAllocate(this.european)
                card = this.probAllocate(this.copper)
            } else if (rare_no > 57 && rare_no <= 85) {
                this.rareAllocate(this.european)
                card = this.probAllocate(this.silver)
            } else {
                this.rareAllocate(this.european)
                card = this.probAllocate(this.gold)
            }
            console.log(card);


            if (roll_no <= 70) {
                this.summon(card, 2, 1, 4, 12)
            } else if (roll_no > 70 && roll_no <= 95) {
                this.summon(card, 3, 3, 12, 36)
            } else {
                this.summon(card, 4, 10, 40, 120)
            }
            return roll_no;
        },
        testUp5(id) {
            let card = undefined
            let luck = 0
            for (var i = 0; i < 4; i++) {
                var roll_res = this.testUp1(id);
                if (roll_res > 70) {
                    luck++;
                }
            }
            if (luck) {
                this.testUp1(id);
            } else {
                var roll_no = Math.floor(Math.random() * 30 + 70)
                let rare_no = this.roll100(this.min);
                this.cardPoolList.forEach(item => {
                    if (item.id == id){
                        this.cardPool = item

                    }
                })
                // 概率分配
                let cardRProb = (100 - this.cardPool.cards.length * this.cardPool.prob) / (this.european.length - this.cardPool.cards.length)
                console.log('cardRProb---',cardRProb);
                this.cardPool.cards.forEach(cardP => {
                    this.european.forEach(cardR => {
                        if (cardP == cardR.name){
                            cardR.prob = this.cardPool.prob

                        }
                    })

                })
                if (rare_no <= 57) {
                    card = this.probAllocate(this.copper)
                } else if (rare_no > 57 && rare_no <= 85) {
                    card = this.probAllocate(this.silver)
                } else {
                    card = this.probAllocate(this.gold)
                }
                if (roll_no > 70 && roll_no <= 95) {
                    this.summon(card, 3, 5, 20, 60)
                } else {
                    this.summon(card, 4, 10, 40, 120)
                }
            }
        },
        draw1ByUp(id) {
            let card = undefined;
            let roll_no = this.roll100(this.min);
            let roll_prob = Math.random()*(101-this.min);
            console.log('roll_prob---',roll_prob);
            this.cardPoolList.forEach(item => {
                if (item.id == id){
                    this.cardPool = item

                }
            })
            // 概率分配
            let cardRProb = (100 - this.cardPool.cards.length * this.cardPool.prob) / (this.european.length - this.cardPool.cards.length)
            console.log('cardRProb---',cardRProb);
            this.cardPool.cards.forEach(cardP => {
                this.european.forEach(cardR => {
                    if (cardP == cardR.name){
                        cardR.prob = this.cardPool.prob

                    }
                })

            })
            for (let i = 0;i < this.european.length;i ++){
                if (this.european[i].prob){
                    roll_prob -= this.european[i].prob
                } else {
                    roll_prob -= cardRProb
                }
                if (roll_prob < 0) {
                    // console.log('i---',i);
                    card = this.european[i]
                    break
                }
            }

            if (roll_no <= 70) {
                this.summon(card, 2, 1, 4, 12)
            } else if (roll_no > 70 && roll_no <= 95) {
                this.summon(card, 3, 5, 20, 60)
            } else {
                this.summon(card, 4, 10, 40, 120)
            }
            return roll_no;
        },
        draw5ByUp(id) {
            let card = undefined
            let luck = 0
            for (var i = 0; i < 4; i++) {
                var roll_res = this.draw1ByUp(id);
                if (roll_res > 70) {
                    luck++;
                }
            }
            if (luck) {
                this.draw1ByUp(id);
            } else {
                var roll_no = Math.floor(Math.random() * 30 + 70)
                let roll_prob = Math.random()*(101-this.min);
                this.cardPoolList.forEach(item => {
                    if (item.id == id){
                        this.cardPool = item

                    }
                })
                // 概率分配
                let cardRProb = (100 - this.cardPool.cards.length * this.cardPool.prob) / (this.european.length - this.cardPool.cards.length)
                console.log('cardRProb---',cardRProb);
                this.cardPool.cards.forEach(cardP => {
                    this.european.forEach(cardR => {
                        if (cardP == cardR.name){
                            cardR.prob = this.cardPool.prob

                        }
                    })

                })
                for (let i = 0;i < this.european.length;i ++){
                    if (this.european[i].prob){
                        roll_prob -= this.european[i].prob
                    } else {
                        roll_prob -= cardRProb
                    }
                    if (roll_prob < 0) {
                        // console.log('i---',i);
                        card = this.european[i]
                        break
                    }
                }
                if (roll_no > 70 && roll_no <= 95) {
                    this.summon(card, 3, 5, 20, 60)
                } else {
                    this.summon(card, 4, 10, 40, 120)
                }
            }
        },
        draw1ByDiamond() {
            let card = undefined;
            let roll_no = this.roll100(this.min);
            if (roll_no <= 70) {
                card = this.european[this.rollcard(this.european)]
                this.summon(card, 2, 1, 4, 12)
            } else if (roll_no > 70 && roll_no <= 95) {
                card = this.european[this.rollcard(this.european)]
                this.summon(card, 3, 5, 20, 60)
            } else {
                card = this.european[this.rollcard(this.european)]
                this.summon(card, 4, 10, 40, 120)
            }
            return roll_no;
        },
        draw5ByDiamond() {
            let card = undefined
            let luck = 0
            for (var i = 0; i < 4; i++) {
                var roll_res = this.draw1ByDiamond();
                if (roll_res > 70) {
                    luck++;
                }
            }
            if (luck) {
                this.draw1ByDiamond();
            } else {
                var roll_no = Math.floor(Math.random() * 30 + 70)
                if (roll_no > 70 && roll_no <= 95) {
                    card = this.european[this.rollcard(this.european)]
                    this.summon(card, 3, 5, 20, 60)
                } else {
                    card = this.european[this.rollcard(this.european)]
                    this.summon(card, 4, 10, 40, 120)
                }
            }
        },
        draw1ByCoin() {
            let card = undefined;
            let roll_no = this.roll100(this.min);
            if (roll_no <= 70) {
                card = this.african[this.rollcard(this.african)];
                if (card.rare == "copper") {
                    console.log("l1" + card.name + "*3");
                    this.inin(card, 1, card.name + "×3");
                } else {
                    console.log("l1" + card.name + "*1");
                    this.inin(card, 1, card.name + "×1");
                }
            } else if (roll_no > 70 && roll_no <= 95) {
                card = this.european[this.rollcard(this.european)]
                this.summon(card, 2, 1, 4, 12)
            } else {
                card = this.european[this.rollcard(this.european)]
                this.summon(card, 3, 5, 20, 60)
            }
            return roll_no;
        },
        draw5ByCoin() {
            let card = undefined
            let luck = 0
            for (var i = 0; i < 4; i++) {
                var roll_res = this.draw1ByCoin();
                if (roll_res > 70) {
                    luck++;
                }
            }
            if (luck) {
                this.draw1ByCoin();
            } else {
                var roll_no = Math.floor(Math.random() * 30 + 70)
                if (roll_no > 70 && roll_no <= 95) {
                    card = this.european[this.rollcard(this.european)]
                    this.summon(card, 2, 1, 4, 12)
                } else {
                    card = this.european[this.rollcard(this.european)]
                    this.summon(card, 3, 5, 20, 60)
                }
            }
        },
        inin(card, type, txt) {
            $('.card .txt').css("opacity", "0")
            card.txt = txt
            card.class = ''
            card.summonType = type
            this.cards.push(card)
            this.$nextTick(() => {
                $('.card').height($('.card').width() * 1.5)
                $('.card .txt').css("margin-top", ($('.card').width() * 1.5 + 10) + 'px')
            })
        },
        btnNew1() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.draw1ByUp(1)
                that.diamonds += 30
            }, 100)
        },
        btnNew5() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.draw5ByUp(1)
                that.diamonds += 150
            }, 100)
        },
        btnCamp1() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.draw1ByUp(12)
                that.diamonds += 30
            }, 100)
        },
        btnCamp5() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.draw5ByUp(12)
                that.diamonds += 150
            }, 100)
        },
        btnDiamond1() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.draw1ByDiamond()
                that.diamonds += 30
            }, 100)
        },
        btnDiamond5() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.draw5ByDiamond()
                that.diamonds += 150

                /*that.european.forEach(item => {
                    if (item.amount != 0) {
                        console.log(item.name + '---' + item.amount)
                    }
                })*/
                /*let finish = 0
                while(finish < that.european.length){
                    that.draw5ByDiamond()
                    that.times += 1
                    that.european.forEach(item => {
                        if (item.rare == 'copper' && item.amount > 840) {
                            finish += 1
                        } else if (item.rare == 'silver' && item.amount > 280){
                            finish += 1
                        } else if (item.rare == 'gold' && item.amount > 70){
                            finish += 1
                        }
                    })
                }
                console.log('5连次数---',that.times);*/
            }, 100)
        },
        btnCoin1() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.draw1ByCoin()
                that.coins += 500
            }, 100)
        },
        btnCoin5() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.draw5ByCoin()
                that.coins += 2500
            }, 100)
        },
        btnTestNew1() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.testUp1(1)
                that.diamonds += 30
            }, 100)
        },
        btnTestNew5() {
            this.cards = []
            let that = this
            setTimeout(function () {
                that.testUp5(1)
                that.diamonds += 150
            }, 100)
        },
        open(i) {
            console.log(i);
            this.cards[i].class = 'open'
            console.log(this.cards);
            let c = this.cards
            this.cards = []
            this.cards = c
            let that = this
            setTimeout(function () {
                $('.card .txt').eq(i).css("opacity", "1")
                switch (that.cards[i].summonType) {
                    case 1:
                        $('.card').eq(i).addClass('l1')
                        break
                    case 2:
                        $('.card').eq(i).addClass('l2')
                        break
                    case 3:
                        $('.card').eq(i).addClass('l3')
                        break
                    case 4:
                        $('.card').eq(i).addClass('l4')
                        break
                }
            }, 500)
            console.log(this.cards[i].class);
        },
        toggleCards(num) {
            this.version = num
            this.init()
        }
    }
}
</script>

