</style>
<style lang="less">
    @import "../assets/less/base";
    .el-dialog__headerbtn{
        margin: 0 !important;
        line-height: 24rem/@fontSize;
    }
    .el-dialog__body{
        overflow: hidden;
        padding: 10px 20px !important;
        .pay{
            margin-bottom: 10px;
        }
        .cardAmount{
            display: flex;
            font-size: 14px;
            line-height: 28px;
            >div{
                flex: auto;
                color: black;
                &:last-child{
                    text-align: right;
                }
            }
            &.gold{
                background-color: #ffd95c;
            }
            &.silver{
                background-color: #cccccc;
